.textGradient {
  background: #469CEE;
  background: -webkit-linear-gradient(to right, #469CEE 0%, #54CBDA 100%);
  background: -moz-linear-gradient(to right, #469CEE 0%, #54CBDA 100%);
  background: linear-gradient(to right, #469CEE 0%, #54CBDA 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
